<template>
  <Header :key="this.role" :role="this.role"></Header>
  <div class="w-page" v-if="showSingIn">
    <Welcome ></Welcome>
    <button class="signin-button" @click="login">Войти с помощью учетной записи УрФУ</button>
  </div>
  <span class="reg" v-if="!showSingIn">{{ name }}</span>
  <button class="reg-button" @click="logout" v-if="!showSingIn">Выйти</button>
  <router-view :key="this.role" :role="this.role" v-if="!showSingIn"></router-view>
</template>

<script>
import Header from "@/components/Header.vue";
import Keycloak from "keycloak-js";
import Welcome from "@/components/Welcome.vue";

const keycloak = new Keycloak({
  url: "https://keys.urfu.ru/auth/",
  realm: "urfu-lk",
  clientId: "rtf-eduload",
  public_key:
      "",
  "token-service":
      "https://keys.urfu.ru/auth/realms/urfu-lk/protocol/openid-connect",
  "account-service": "https://keys.urfu.ru/auth/realms/urfu-lk/account",
  "tokens-not-before": 0,
});

export default {
  mounted() {
    this.updateKC();
  },
  components: {
    Header,
    Welcome
  },
  data() {
    return {
      role: 'write',
      showSingIn: false,
      name: "Not Auth",
      isAuth: false,
    };
  },
  methods: {
    async updateKC()
    {
      await keycloak.init({
        onLoad: "check-sso",
      });
      if (keycloak.authenticated) {
        this.name = keycloak.tokenParsed.user.person.title;
        this.showSingIn = false;
        let url = 'http://212.193.88.196:8080/checkuser?login=' + keycloak.tokenParsed.user.person.email + '&isKeycloak=1&samAccountName=' + keycloak.tokenParsed.user.person.id;
        await fetch(url).then((resp) => resp.text())
            .then((razm) => (this.role = razm));
        console.log(this.role);
      }
    },
    async login() {
      if (!keycloak.authenticated) {
        await keycloak.login();
      } else {
        this.name = keycloak.tokenParsed.user.person.title;
        this.isAuth = true;
      }
    },
    async logout() {
      if (keycloak.authenticated) {
        await keycloak.logout();
      }
    },
  },
};
</script>

<style>
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}
.signin-button {
  padding: 10px;
  border-radius: 10px;
  margin-left: 18%;
  margin-top: 15px;
  cursor: pointer;
  display: inline-flex;
  width: 200px;
  font-size: 16px;
}
.w-page{
  display: flex;
  align-items: center;

}
.reg-button {
  padding: 10px;
  border-radius: 10px;
  font-size: 18px;
  margin-left: 20px;
  margin-top: 15px;
  cursor: pointer;
  display: inline-flex;
}

.reg {
  display: inline-flex;
  margin-top: 15px;
  margin-left: 20px;
  font-size: 20px;
}
</style>
