<template>
  <div style="display: flex" class="reg" v-if="isLoading">Загрузка таблицы...</div>
  <div class="table" ><AGGrid
      :gridData="gridData"
      :columnDefs="columnDefs"
      :defaultColDef="defaultColDef"
      :getRowId="getRowId"
      :localeText="ruLocale"
  ></AGGrid></div>
</template>

<script>
import AGGrid from "@/components/AGGrid.vue";
import infoCellRenderer from "@/components/infoCellRenderer";
import AG_GRID_LOCALE_RU from "@/components/locale.ru.js";

export default {
  props: ['role'],
  mounted() {
    this.fetchJson();
    this.ruLocale = AG_GRID_LOCALE_RU;
  },
  components: {
    AGGrid,
    infoCellRenderer
  },
  data() {
    return {
      isLoading: true,
      gridData: [],
      columnDefs: [
        { headerName: "", field: "tkey", cellRenderer: "infoCellRenderer", width: 70},
        { headerName: "№", valueGetter: params => {
            return params.node ? params.node.rowIndex + 1 : null;
          }, width: 60, },
        { headerName: "ФИО", field: "name", width: 280, filter: true, floatingFilter: true,},
        { headerName: "Должность/Место работы", field: "infoWorkPlaces", width: 280, filter: true, floatingFilter: true,},
        { headerName: "Ставка", valueGetter: params => {
            return params.data.stake;
          },
          valueSetter: params => {
            let new_value = parseFloat(params.newValue).toFixed(2);
            let url = 'http://212.193.88.196:8080/changestake?tkey=' + params.data.tkey + '&value=' + new_value;
            fetch(url);
            params.data.stake = new_value;
            return true;
          },  editable: this.isEditable(this.role), width: 100},
        { headerName: "Часы на ставку",
          valueGetter: params => {
            return params.data.hoursOnStake;
          },
          valueSetter: params => {
            let new_value = parseFloat(params.newValue).toFixed(2);
            let url = 'http://212.193.88.196:8080/changehours?guidPerson1C=' + params.data.guidPerson1C + '&value=' + new_value;
            fetch(url);
            params.data.hoursOnStake = new_value;
            params.data.hours = (params.data.bHoursPlaned - params.data.hoursOnStake).toFixed(2);
            return true;
          },
          editable: this.isEditable(this.role), width: 100},
        { headerName: "Кол-во часов на почасовую оплату",
          valueGetter: params => {
            return params.data.hours;
          },
          valueSetter: params => {
            let new_value = parseFloat(params.newValue).toFixed(2);
            params.data.hours = new_value;
            return true;
          }, width: 130},
        {
          headerName: 'Бюджет',
          children: [
            { headerName: "Планируемое кол-во часов", field: "bHoursPlaned", width: 140},
            { headerName: "Фактическое кол-во часов", field: "bHoursReal", width: 140},
            { headerName: "Разница", field: "bHoursDiff", width: 100},
          ],
        },
        {
          headerName: 'Контракт',
          children: [
            {headerName: "Планируемое кол-во часов", field: "cHoursPlaned", width: 140},
            {headerName: "Фактическое кол-во часов", field: "cHoursReal", width: 140},
            {headerName: "Разница", field: "cHoursDiff", width: 100},
          ],
        },
        {
          headerName: 'Общее',
          children: [
            { headerName: "Планируемое кол-во часов", field: "hoursPlaned", width: 140 },
            { headerName: "Фактическое кол-во часов", field: "hoursReal", width: 140 },
            { headerName: "Разница", field: "hoursDiff", width: 100 },
          ],
        },
        { headerName: "Год", field: "year", width: 130, filter: true, floatingFilter: true,},
      ],
      defaultColDef: {
        sortable: true,
        resizable: true,
        spanHeaderHeight: true,
        wrapHeaderText: true,
        minWidth: 50,
        wrapText: true,
        autoHeight: true,
        autoHeaderHeight: true,
      },
    };
  },
  methods: {
    async fetchJson() {
      let json;
      await fetch("http://212.193.88.196:8080/loaddata")
        .then((resp) => resp.text())
        .then((razm) => (json = razm));
      this.gridData = await JSON.parse(json);
      this.isLoading = false;
    },
    isEditable(role) {
      return role == 'admin' || role == 'write';
    }
  },
};


</script>

<style scoped>

.table {
  margin: 20px;
  display: flex;
  height: 900px;
  width: 2320px;
}
</style>
