<template>
  <button class="reg-button" @click="goBack" style="display: flex" >Назад</button>

  <div class="reg" style="display: flex" >Преподаватель: {{this.name}}, {{this.info}}, {{this.year}}</div>
  <div class="reg" style="display: flex" >Кол-во часов на почасовую оплату: {{this.hours}}</div>

  <div class="table" ><AGGrid
      :gridData="gridData"
      :columnDefs="columnDefs"
      :defaultColDef="defaultColDef"
      :getRowId="getRowId"
      :localeText="ruLocale"

  ></AGGrid></div>
</template>

<script>
import AGGrid from "@/components/AGGrid.vue";
import AG_GRID_LOCALE_RU from "@/components/locale.ru.js";
import checkboxCellRenderer from "@/components/checkboxCellRenderer";

export default {
  props: ['role'],
  mounted() {
    this.fetchJson();
    this.ruLocale = AG_GRID_LOCALE_RU;
    this.name = this.$route.query.name;
    this.info = this.$route.query.info;
    this.year = this.$route.query.year;
    this.hours = this.$route.query.hours;
  },
  components: {
    AGGrid,
    checkboxCellRenderer
  },
  data() {
    return {
      gridData: [],
      columnDefs: [
        { headerName: "№", valueGetter: params => {
            return params.node ? params.node.rowIndex + 1 : null;
          }, width: 60, },
        { headerName: "Дисциплина", field: "disciplineName", width: 300, filter: true, floatingFilter: true,},
        { headerName: "Академическая группа", field: "groupsHistory", width: 160, filter: true, floatingFilter: true,},
        { headerName: "Семестр", field: "semester",  width: 160, filter: true, floatingFilter: true,},
        { headerName: "Вид нагрузки", field: "loadType", width: 195, filter: true, floatingFilter: true,},
        { headerName: "Формирующая кафедра", field: "formingDivisionuuid", width: 300, filter: true, floatingFilter: true,},
        { headerName: "Читающая кафедра", field: "readingDivisionuuid", width: 300, filter: true, floatingFilter: true,},
        { headerName: "Тип нагрузки", field: "compensationType", width: 140, filter: true, floatingFilter: true,},
        { headerName: "Планируемое кол-во часов", field: "plannedHours", width: 140,},
        { headerName: "Фактическое кол-во часов", valueGetter: params => {
            return params.data.realHours;
          },
          valueSetter: params => {
            let new_value = parseFloat(params.newValue).toFixed(2);
            let url = 'http://212.193.88.196:8080/changerealhours?id=' + params.data.id + '&value=' + new_value;
            fetch(url);
            params.data.realHours = new_value;
            params.data.diff = (params.data.plannedHours - params.data.realHours).toFixed(2);
            return true;
          }, editable: this.isEditable(this.role), width: 140,},
        { headerName: "Разница", field: "diff", width: 100},
        { headerName: "Почасовая оплата", field: "isHour", width: 120, cellRenderer: "checkboxCellRenderer" },
      ],
      defaultColDef: {
        sortable: true,
        resizable: true,
        spanHeaderHeight: true,
        wrapHeaderText: true,
        minWidth: 50,
        wrapText: true,
        autoHeight: true,
        autoHeaderHeight: true,
      },
    };
  },
  methods: {
    goBack() {
      window.location.href = '/';
    },
    async fetchJson() {
      let json;
      await fetch("http://212.193.88.196:8080/loaddatabytkey?tkey=" + this.$route.query.tkey)
          .then((resp) => resp.text())
          .then((razm) => (json = razm));
      this.gridData = JSON.parse(json);
    },
    isEditable(role) {
      return role == 'admin' || role == 'write';
    }
  },
};
</script>

<style scoped>

.table {
  margin: 20px;
  display: flex;
  height: 830px;
  width: 2180px;
}
</style>